import React, { useState, useEffect } from 'react';
import { MdLocationOn, MdWork, MdEmail } from 'react-icons/md';
import { FaWhatsapp } from 'react-icons/fa';

import api from '~/services/api';
import { Container, Content, List, Item, ItemImage } from './styles';

export default function HomePage() {
  const [avatar, setAvatar] = useState('');
  useEffect(() => {
    async function loadAvatar() {
      const { data } = await api.get('/users/igorjung');

      setAvatar(data.avatar_url);
    }

    loadAvatar();
  }, []);

  return (
    <Container>
      <Content>
        <h1>Contact</h1>
        <List>
          <ItemImage>
            <img src={avatar} alt="IgorJung" />
          </ItemImage>
          <Item>
            <MdLocationOn size={20} color="#ddd" />
            <strong>Volta Redonda, Brazil</strong>
          </Item>
          <Item>
            <MdWork size={20} color="#ddd" />
            <strong>Software Developer</strong>
          </Item>
          <Item>
            <MdEmail size={20} color="#ddd" />
            <strong>igor.jung@outlook.com</strong>
          </Item>
          <Item>
            <FaWhatsapp size={20} color="#ddd" />
            <strong>+55 (24) 98848-2727</strong>
          </Item>
        </List>
      </Content>
    </Container>
  );
}
